import React from 'react'
import { connect } from 'react-redux'

import { disconnetCall, answerCall } from '../../redux/calling/calling-action'

import './incoming-call.css'

const Incoming = ({ data, answerCall, disconnetCall }) => {
    console.log(data);
    const ansCall = () => {
        answerCall()
    }
    const dropCall = () => {
        disconnetCall()
    }
    return (
        <>
            {
                data ?

                    <div className='call-pop'>
                        <div className='heading'>
                            <h4>Navneet</h4>
                        </div>
                        <div className="btn-c">
                            <button className='btn btn-success' onClick={ansCall}> <span><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-telephone-fill text-white" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M1.885.511a1.745 1.745 0 0 1 2.61.163L6.29 2.98c.329.423.445.974.315 1.494l-.547 2.19a.678.678 0 0 0 .178.643l2.457 2.457a.678.678 0 0 0 .644.178l2.189-.547a1.745 1.745 0 0 1 1.494.315l2.306 1.794c.829.645.905 1.87.163 2.611l-1.034 1.034c-.74.74-1.846 1.065-2.877.702a18.634 18.634 0 0 1-7.01-4.42 18.634 18.634 0 0 1-4.42-7.009c-.362-1.03-.037-2.137.703-2.877L1.885.511z" />
                            </svg>{' '} </span> Answer</button>
                            <button className='btn btn-danger' onClick={dropCall}><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-telephone-x-fill" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M1.885.511a1.745 1.745 0 0 1 2.61.163L6.29 2.98c.329.423.445.974.315 1.494l-.547 2.19a.678.678 0 0 0 .178.643l2.457 2.457a.678.678 0 0 0 .644.178l2.189-.547a1.745 1.745 0 0 1 1.494.315l2.306 1.794c.829.645.905 1.87.163 2.611l-1.034 1.034c-.74.74-1.846 1.065-2.877.702a18.634 18.634 0 0 1-7.01-4.42 18.634 18.634 0 0 1-4.42-7.009c-.362-1.03-.037-2.137.703-2.877L1.885.511zm9.261 1.135a.5.5 0 0 1 .708 0L13 2.793l1.146-1.147a.5.5 0 0 1 .708.708L13.707 3.5l1.147 1.146a.5.5 0 0 1-.708.708L13 4.207l-1.146 1.147a.5.5 0 0 1-.708-.708L12.293 3.5l-1.147-1.146a.5.5 0 0 1 0-.708z" />
                            </svg> Drop</button>
                        </div>
                    </div>
                    : null

            }
        </>
    )
}

const mapStateToProps = state => ({
    data: state.incomingcall
})

const mapDispatchToProps = dispatch => ({
    disconnetCall: () => dispatch(disconnetCall()),
    answerCall: (data = {}) => dispatch(answerCall(data))
})

export default connect(mapStateToProps, mapDispatchToProps)(Incoming);
